import React, {Component} from "react";
import Fade from "@material-ui/core/Fade";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import NavLink from "react-router-dom/NavLink";
import Divider from "@material-ui/core/Divider";

import {isMobile} from "react-device-detect";

const buttonFontSize = isMobile ? 0.55 : 0.8;
const divFontSize = isMobile ? 0.6 : 0.4;

const styles = {
  inlineButton: {
    margin: "5px 10px",
  },
  button: {
    "font-size": "calc(" + buttonFontSize + "em + " + buttonFontSize + "vw)"
  },
  IntroDiv: {
    "font-size": "calc(" + divFontSize + "em + " + divFontSize + "vw)" 
    },
    divider: {
        margin: "0px 15px"
    },
    ProjectHeader: {
        "font-size": "calc(1.4em + 1.4vw)",
        "font-weight": "200",
        "font-family": "Roboto",
        "text-align": "left"
    },
    ProjectContent: {
        "font-size": "calc(0.6em + 0.6vw)",
        "font-weight": "300",
        "font-family": "Roboto",
        "text-align": "left",
        "padding": "3% 3%"
    },
    ExpandButton: {
        "all": "inherit",
        "width": "100%"
    },
    stronger: {
        "font-weight": "400"
    }
};
class Projects extends Component {
    constructor(props) {
        super(props);
        // create a ref to store the textInput DOM element this.faceContent =
        // React.createRef();
        this.toggleFaceHidden = this
            .toggleFaceHidden
            .bind(this);
        this.toggleLocalHidden = this
            .toggleLocalHidden
            .bind(this);
        this.toggleBeaconHidden = this
            .toggleBeaconHidden
            .bind(this);
        this.toggleLightHidden = this
            .toggleLightHidden
            .bind(this);
        this.state = {
            showFace: false,
            showLocal: false,
            showBeacon: false,
            showLight: false,
            faceSymbol: " +",
            localSymbol: " +",
            beaconSymbol: " +",
            lightSymbol: " +"
        }
    }
    toggleFaceHidden() {
        let newFaceSymbol = " +";
        if (this.state.faceSymbol === " +") {
            newFaceSymbol = " -";
        }
        this.setState({
            showFace: !this.state.showFace,
            faceSymbol: newFaceSymbol
        });
    }
    toggleLocalHidden() {
        let newLocalSymbol = " +";
        if (this.state.localSymbol === " +") {
            newLocalSymbol = " -";
        }
        this.setState({
            showLocal: !this.state.showLocal,
            localSymbol: newLocalSymbol
        });
    }
    toggleBeaconHidden() {
        let newBeaconSymbol = " +";
        if (this.state.beaconSymbol === " +") {
            newBeaconSymbol = " -";
        }
        this.setState({
            showBeacon: !this.state.showBeacon,
            beaconSymbol: newBeaconSymbol
        });
    }
    toggleLightHidden() {
        let newLightSymbol = " +";
        if (this.state.lightSymbol === " +") {
            newLightSymbol = " -";
        }
        this.setState({
            showLight: !this.state.showLight,
            lightSymbol: newLightSymbol
        });
    }
    render() {
        return (
            <Fade in={true} timeout={500}>
                <div className={this.props.classes.ProjectsDiv}>
                    <Paper
                        elevation={4}
                        style={{
                        margin: "5px 5%",
                        padding: "5px"
                    }}>
                        <div className="App-intro">
                            <div className={this.props.classes.ProjectHeader}>
                                <button
                                    onClick={this.toggleFaceHidden}
                                    className={this.props.classes.ExpandButton}
                                    aria-expanded="false">
                                    Make a Face
                                    <span className={this.props.classes.stronger}>{this.state.faceSymbol}</span>
                                </button>
                            </div>
                            <div
                                style={{
                                display: this.state.showFace
                                    ? 'block'
                                    : 'none'
                            }}
                                className={this.props.classes.ProjectContent}>
                                Context: 2018 Home Depot Deep Learning Hackathon
                                <br/>
                                <br/>
                                Languages/Technologies: (i)Python, Javascript, HTML/CSS, Keras
                                <br/>
                                <br/>
                                Product: Webapp to rate facial expression similarity
                            </div>
                            <Divider/>
                            <div className={this.props.classes.ProjectHeader}>
                                <button
                                    onClick={this.toggleLocalHidden}
                                    className={this.props.classes.ExpandButton}
                                    aria-expanded="false">
                                    Local Neural Style Transfer
                                    <span className={this.props.classes.stronger}>{this.state.localSymbol}</span>
                                </button>
                            </div>
                            <div
                                style={{
                                display: this.state.showLocal
                                    ? 'block'
                                    : 'none'
                            }}
                                className={this.props.classes.ProjectContent}>
                                Context: Mobile and Web Application Development Research Lab
                                <br/>
                                <br/>
                                Languages/Technologies: (i)Python, Java, Android, Tensorflow
                                <br/>
                                <br/>
                                Product: Android application to perform Style Transfer using neural networks
                            </div>
                            <Divider/>
                            <div className={this.props.classes.ProjectHeader}>
                                <button
                                    onClick={this.toggleBeaconHidden}
                                    className={this.props.classes.ExpandButton}
                                    aria-expanded="false">
                                    Beacon
                                    <span className={this.props.classes.stronger}>{this.state.beaconSymbol}</span>
                                </button>
                            </div>
                            <div
                                style={{
                                display: this.state.showBeacon
                                    ? 'block'
                                    : 'none'
                            }}
                                className={this.props.classes.ProjectContent}>
                                Context: HackTJ 2018
                                <br/>
                                <br/>
                                Languages/Technologies: Dart, Python, HTML/CSS/JS, Flutter
                                <br/>
                                <br/>
                                Product: Multi-platform mobile app to help school administrators locate students
                                in a crisis
                            </div>
                            <Divider/>
                            <div className={this.props.classes.ProjectHeader}>
                                <button
                                    onClick={this.toggleLightHidden}
                                    className={this.props.classes.ExpandButton}
                                    aria-expanded="false">
                                    Lighthouse
                                    <span className={this.props.classes.stronger}>{this.state.lightSymbol}</span>
                                </button>
                            </div>
                            <div
                                style={{
                                display: this.state.showLight
                                    ? 'block'
                                    : 'none'
                            }}
                                className={this.props.classes.ProjectContent}>
                                Context: HackTJ 2017
                                <br/>
                                <br/>
                                Languages/Technologies: Javascript, Python, React Native
                                <br/>
                                <br/>
                                Product: Multi-platform mobile app to help school administrators locate students
                                in a crisis
                            </div>
                        </div>
                        <Button
                            variant="outlined"
                            color="secondary"
                            className={this.props.classes.inlineButton}>
                            <a
                                className="navlinks small-link"
                                onClick={(event) => {
                                event.preventDefault();
                                window.open("static/Alexander_Gurung_resume.pdf");
                            }}>
                                My CV
                            </a>{" "}
                        </Button>
                        <Button
                            variant="outlined"
                            color="primary"
                            className={this.props.classes.button}>
                            <NavLink className="navlinks" to="/">
                                Home!
                            </NavLink>
                        </Button>
                        <Button
                            variant="outlined"
                            color="secondary"
                            className={this.props.classes.inlineButton}>
                            <a
                                className="navlinks small-link"
                                onClick={(event) => {
                                event.preventDefault();
                                window.open("https://github.com/Alex-Gurung");
                            }}>
                                Github
                            </a>{" "}
                        </Button>
                    </Paper>
                </div>
            </Fade>
        );
    }
}
Projects.propTypes = {
    children: PropTypes.node,
    classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Projects);



