import React, { Component } from "react";
import Fade from "@material-ui/core/Fade";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import NavLink from "react-router-dom/NavLink";
import Divider from "@material-ui/core/Divider";

import {isMobile} from "react-device-detect";

const buttonFontSize = isMobile ? 0.55 : 0.8;
const divFontSize = isMobile ? 0.6 : 0.4;

const styles = {
  inlineButton: {
    margin: "5px 10px",
  },
  button: {
    "font-size": "calc(" + buttonFontSize + "em + " + buttonFontSize + "vw)"
  },
  IntroDiv: {
    "font-size": "calc(" + divFontSize + "em + " + divFontSize + "vw)" 
  },
  divider: {
    margin: "0px 15px"
  }
};
class Intro extends Component {
  render() {
    return (
      <Fade in={true} timeout={500}>
        <div className={this.props.classes.IntroDiv}>
          <Paper elevation={4} style={{margin: "5px 5%", padding: "5px"}}>
          <div className="App-intro">
            Hey there! My name's Alex, I'm
            currently an undergrad at
            <Button
              variant="outlined"
              color="primary"
              className={this.props.classes.inlineButton}
            >
              <a
                className="navlinks small-link gatech"
                href="https://gatech.edu"
              >
                Georgia Tech
              </a>{" "}
            </Button>{" "}
            pursuing a Bachelor's Degree in Computer Science and a minor in Linguistics!
            <Divider />
            I'm currently looking for a summer internship, although I'm open to part-time opportunities during the year.
            <Divider/>
            I'm primarily interested in machine learning and software development, and have experience in both!    <Divider />
          </div>
          <Button
            variant="outlined"
            color="secondary"
            className={this.props.classes.inlineButton}
          >
            <a
              className="navlinks small-link"
              onClick={(event) => { event.preventDefault(); window.open("static/Alexander_Gurung_resume.pdf"); }}
            >
              My CV
            </a>{" "}
          </Button>
          <Button
            variant="outlined"
            color="primary"
            className={this.props.classes.button}
          >
            <NavLink className="navlinks" to="/projects">
              Projects!
            </NavLink>
          </Button>
          <Button
            variant="outlined"
            color="secondary"
            className={this.props.classes.inlineButton}
          >
            <a
              className="navlinks small-link"
              onClick={(event) => { event.preventDefault(); window.open("https://github.com/Alex-Gurung"); }}
            >
              Github
            </a>{" "}
          </Button>
          </Paper>
        </div>
      </Fade>
    );
  }
}
Intro.propTypes = {
  children: PropTypes.node,
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Intro);



